body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  position: relative;
  height: 600px;
  overflow: hidden;
}

.header__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(315deg, #ffd3a9 0%, #9795ef 74%);
  transform: skewY(-6deg);
  transform-origin: top left;
}

.headerGreeting {
  margin: 0;
  padding: 200px 0;
  text-align: center;
  color: white;
}

.headerGreeting2 {
  margin: -180px 0 0 0;
  text-align: center;
  color: white;
}

