@media screen and (max-width:767px) {
  .about-wrapper {
    flex-direction: column;
    padding-bottom: 5(rem);
    display: flex;
    align-items: center;
  }

  .about-wrapper__image {
    /* display: block; */
    height: 100%;
    /* align-items: center;
    justify-content: center;
    width:80%; */
    margin: auto;
  } 
  
  .about-wrapper__info{
    padding-top: 20px;
    height: 100%;
    /* justify-content: left;
    flex-direction: column;
    width: 80%; */
    margin: auto;
  }
  
  .about-wrapper__info-text {
    padding-top: 60px;
        font-size: 1em;
        width: 80%;
        text-align: justify;
        /* word-spacing:-2px; */
        margin: auto;
      
  }
}

@media screen and (min-width: 768px) {
  .about-wrapper {
    padding-bottom: 5(rem);
    display: flex;
  }

  .about-wrapper__image {
    flex: 1;
    margin-right: 20px;
    height: 100%;
    align-items: center;
    justify-content: center;
  } 
  
  .about-wrapper__info{
    flex: 1;
    height: 100%;
    justify-content: left;
    flex-direction: column;
  }
  
  .about-wrapper__info-text {
    padding-top: 2%;
        font-size: 1.3em;
        width: 80%;
        text-align: justify;
        word-spacing:-2px;
        margin: auto;
        padding-right: 20px;
      
  }
}

@media screen and (min-width: 900px) {
  .about-wrapper__info-text {
    margin: 0;
  }
}
.projImageContainer {
  max-height: auto;
  max-width: 400px;
  object-fit: cover;
  overflow: hidden;
}

.projImage {
  max-height: auto;
  max-width: 400px;
  object-fit: cover;
  overflow: hidden;
}

.project {
  background: #f5faff;
  margin: 5px 5px;
  padding: 10px 10px;
  cursor: pointer;
  max-width: 500px;
  height: auto;
  
}

.projects {
	background-color: #8886da;
	text-align: center;
	padding: 0 30px 80px 30px;
}

.contact {
  display: flex;
  background-color: #ffffff;
	text-align: justify;
}


.half_background {
  background: linear-gradient(to top, rgb(209, 182, 231) 50%, transparent 50%);
}

.projectTitle {
  padding-top: 100px;
  color: white;
}

@media screen and (min-width: 900px) {
.projectTitle {
  margin-left: 0;
  margin-right: auto;
  color: white;
}
}

@media screen and (max-width:767px) {
  .contactContainer {
    display: flex;
    align-items: start;
    flex-direction: column;
    gap: 5px;
  }

  .contactPicBox {
    height: 100%;
    padding-top: 100px;
    padding-left: 100px;
  }

  .contactBox {
    width: 30%;
    align-self: center;
  }
  
  .contactTitle {
    padding: 100px 0px 20px 30px;
    margin-left:0;
    margin-right: auto;
  }
  
  .contactEmail {
    padding: 20px 0px 20px 30px;
    margin-left:0;
    margin-right: auto;
    text-decoration: none;
    color: black;
  }
  
  .contactPic {
    width: 70%;
    height: 70%;
    border-radius: 50%;
  }
  
  .gh {
    padding: 40px 0px 20px 30px;
    margin-left:0;
    margin-right: auto;
    align-items: left;
  }
}

@media screen and (min-width:768px) {
  .contactContainer {
    display: flex;
    align-items: start;
    flex-direction: column;
    gap: 10px;
  }

  .contactPicBox {
    height: 100%;
    padding-top: 120px;
    padding-left: 80px;
  }

  .contactBox {
    width: 30%;
    align-self: center;
  }
  
  .contactTitle {
    padding: 100px 0px 20px 100px;
    margin-left:0;
    margin-right: auto;
  }
  
  .contactEmail {
    padding: 20px 0px 20px 100px;
    margin-left:0;
    margin-right: auto;
    text-decoration: none;
    color: black;
  }
  
  .contactPic {
    width: 150px;
    height: 150px;
    border-radius: 50%;
  }
  
  .gh {
    padding: 40px 0px 20px 100px;
    margin-left:0;
    margin-right: auto;
    align-items: left;
  }
}

.aboutPic {
  max-width: 60%;
}

section {
  padding: 10rem 0rem;
}